import React, { useEffect, useState } from "react";
import utility from "../styles.module/utility.module.css";
import style from "../styles.module/Navbar.module.css";
import cx from "classnames";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import Country from "./navCompo/Country";
import Loginmouseover from "./navCompo/Loginmouseover";
import "../pages/login.css";
import { useDisclosure, Box, HStack, Image, Text } from "@chakra-ui/react";
import { useMediaQuery } from "@chakra-ui/react";

const Navbar = () => {
  const navigate = useNavigate();

  // navbar api fetch here
  const [ismobilescreen] = useMediaQuery(`(max-width: 820px)`);
  // console.log(ismobilescreen)
  const { err, isAuth } = useSelector((state) => state.auth);

  useEffect(() => {
    if (isAuth) {
      onClose();
    }
  }, [isAuth]);

  const { onOpen, onClose } = useDisclosure();

  const [country, setcountry] = useState(false);
  const [Loginmouse, setLoginmouse] = useState(false);
  // console.log(country);
  // useEffect(() => {
  //   console.log(country);
  // }, []);
  return (
    <Box w={"100%"}>
      <HStack
        maxW={"1400px"}
        w={{
          base: "100%",
          md: "100%",
          lg: "1000px",
          xl: "1200px",
          "2xl": "1400px",
        }}
        margin={"auto"}
        px={5}
        py={2}
        justifyContent={"space-between"}
        alignItems={"center"}
      >
        <Image
          cursor={"pointer"}
          w={"100px"}
          src="https://cdn.modesens.com/static/img/20190228newlogo-black.svg"
          alt=""
          onClick={() => navigate("/")}
        />
        {
          // if this is not a mobile device
          !ismobilescreen && (
            <HStack justifyContent={"center"} gap={"10px"}>
              <Text
                name={"WOMEN"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/shop-women-clothing")}
              >
                WOMEN
              </Text>
              <Text
                name={"MEN"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/shop-men-clothing")}
              >
                MEN
              </Text>
              <Text
                name={"BEAUTY"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/beauty")}
              >
                BEAUTY
              </Text>
              <Text
                name={"KIDS"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/kids")}
              >
                KIDS
              </Text>
              <Text
                name={"HOME"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/")}
              >
                HOME
              </Text>
              <Text
                name={"OFFERS"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/offers")}
              >
                OFFERS
              </Text>
              <Text
                name={"DESIGNERS"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/designers")}
              >
                DESIGNERS
              </Text>
              <Text
                name={"COMMUNITY"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/community")}
              >
                COMMUNITY
              </Text>
              <Text
                name={"WHY"}
                className={cx(style.subcateg, utility.cursurhover)}
                onClick={() => navigate("/why")}
              >
                WHY MODESENS
              </Text>
            </HStack>
          )
        }

        <HStack>
          <Image
            className={style.country}
            onClick={() => setcountry(!country)}
            src="data:image/jpeg;base64,"
            alt=""
          />

          <Image
            className={cx(style.rightsubcateg, utility.cursurhover)}
            src="https://cdn.modesens.com/static/img/20200612shopping_bag2.svg"
            alt=""
          />
          {Loginmouse ? (
            <Loginmouseover
              setLoginmouse={setLoginmouse}
              Loginmouse={Loginmouse}
              onOpen={onOpen}
            />
          ) : null}
          <Image
            onMouseEnter={() => setLoginmouse(true)}
            src="https://cdn.modesens.com/static/img/20200612account_b2.svg"
            alt=""
            onClick={onOpen}
          />

          <HStack
            style={{
              display: "flex",
              alignItems: "center",
              padding: "2px",
            }}
            className={utility.cursurhover}
          >
            <Image
              src="https://cdn.modesens.com/static/img/20200612search_img1.svg"
              alt=""
            />

            <Text
              style={{
                marginLeft: "5px",
                fontWeight: 500,
                letterSpacing: 0.2,
              }}
            >
              {" "}
              SEARCH
            </Text>
          </HStack>
          {country ? <Country /> : null}
        </HStack>
      </HStack>
    </Box>
  );
};

export default Navbar;
